<template>
  <div v-bind:id="id" class="hello">
    <h1>{{ msg }}</h1>
    <p>This is where I would put a pretty image as the background.</p>
  </div>
</template>

<script>
export default {
  name: 'hello',
  props: ['id', 'msg']
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.hello {
  height: 50vh;
}
</style>



